@import url('https://fonts.googleapis.com/css?family=Bebas+Neue&display=swap');

$font-bebas : 'Bebas Neue';
$font-nunito : 'Nunito';
$font-sand : 'Quicksand'; //used
$font-rubik : 'Rubik';
$font-atlatsi : 'Atlatsi';
$font-tech : 'Share Tech Mono'; //usedd

//-l = lighter, -d = darker
$cyan-l: #a5c2b8;//old rgb(16, 194, 134);
$cyan-l-d : #99aca5;
$cyan : #2abb86;
$cyan-d : #1a8a6c;
$cyan-d-d : #17725a;
$grey-blue : #18584a;
$grey-blue-d : #17473d;

$border: #2e463e;
$border-d: #1e2522;
$border2:#1b2e28;

$dark : #222424;
$grey : #343636;

$black : rgb(15, 20, 19);
$black-t : rgba(9, 22, 20, 0.4);
//rgba(38, 68, 63, 0.5);

//DONT FORGET TO UPDATE GRADIENTS



@mixin gradient-app {
    background: rgb(17, 20, 20);
    background: -moz-linear-gradient(left, rgb(17, 20, 20) 0%, rgb(27, 34, 33) 100%);
    background: -webkit-linear-gradient(left, rgb(17, 20, 20) 0%, rgb(27, 34, 33) 100%);
    background: linear-gradient(to right, rgb(18, 22, 22) 0%, rgb(26, 46, 41) 50%, rgb(18, 22, 22) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#111414', endColorstr='#1b2221',GradientType=1 ); /* IE6-9 */
}

@mixin gradient-title {
    background: rgb(17, 20, 20);
    background: -moz-linear-gradient(left, rgb(17, 20, 20) 0%, rgb(27, 34, 33) 100%);
    background: -webkit-linear-gradient(left, rgb(17, 20, 20) 0%, rgb(27, 34, 33) 100%);
    background: linear-gradient(to right, rgb(15, 24, 24, 0.1) 0%, $black-t 20%, $black-t 80%, rgb(15, 24, 24, 0.1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#111414', endColorstr='#1b2221',GradientType=1 ); /* IE6-9 */
}

@mixin gradient-box {
    background: linear-gradient(to bottom, $black-t 0%, rgba(0, 0, 0, 0) 100%);
}

