body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{background:#111414;background:linear-gradient(to right, #121616 0%, #1a2e29 50%, #121616 100%);filter:progid:DXImageTransform.Microsoft.gradient( startColorstr="#111414", endColorstr="#1b2221",GradientType=1 );height:auto;font-family:"Rubik"}
.home-box{background:#111414;background:linear-gradient(to right, #121616 0%, #1a2e29 50%, #121616 100%);filter:progid:DXImageTransform.Microsoft.gradient( startColorstr="#111414", endColorstr="#1b2221",GradientType=1 );height:100vh;display:flex;flex-direction:column;justify-content:space-between}.home-links-box{display:flex;flex-direction:row;align-items:center;justify-content:center;width:100%;height:auto;z-index:2;margin-bottom:20px}.particles{position:absolute;width:100%;height:100%}.intro{font-family:"Quicksand";align-self:center;text-align:center;color:#1a8a6c;font-size:100%}.intro h1{color:#a5c2b8}@media(min-width: 812px){.intro{font-size:120%}}@media(max-width: 372px){.home-links-box{flex-direction:column}}
header{margin:0 auto;padding:10px 0;width:100vw;display:inline-flex;justify-content:space-between}.wrapper-svg{width:10vw;height:auto}
.nav-box{width:10vw;height:auto;display:flex;flex-direction:column;justify-content:center}.nav-svg{width:calc(15px + 1vw);margin:auto;display:block;padding:auto;fill:#a5c2b8}.nav-svg:hover{fill:#99aca5;cursor:pointer}.nav-list{display:flex;flex-direction:column;align-items:flex-end;justify-content:center;width:auto;margin-top:10px}.nav-link{padding:5px;width:auto}.nav-link a{font-family:"Quicksand";font-weight:bold;text-transform:uppercase;-webkit-text-emphasis:none;text-emphasis:none;text-decoration:none;color:#17473d;padding:5px;background-color:rgba(16,20,17,.349)}.nav-link a:hover{color:#a5c2b8;text-shadow:0 0 2px #2abb86}@media(min-width: 812px){.nav-list{align-items:center;width:auto}.nav-link a{font-size:110%}}
.logo-svg{width:calc(20px + 1vw);margin:0 auto;display:block;opacity:1}.logo-svg:hover{opacity:.8;cursor:pointer}.logo-svg .svg-style{fill:#a5c2b8}
.home-link{display:flex;justify-content:center;align-items:center;width:auto;height:auto;font-size:130%;margin:20px 30px}.home-link a{color:#17473d;text-decoration:none;font-family:"Quicksand";font-weight:bold;height:auto;padding:5px;border:solid 2px #2e463e;border-radius:5px;box-shadow:0 1px 5px #0f1413;color:#a5c2b8;background:linear-gradient(to bottom, rgba(9, 22, 20, 0.4) 0%, rgba(0, 0, 0, 0) 100%);text-transform:uppercase}.home-link a:hover{color:#99aca5;box-shadow:inset 0 0 5px #0f1413}
.about-box{height:auto;width:100%;margin:0 auto;margin-bottom:0px;padding:20vh 0;background-color:rgba(9,12,12,.35);box-shadow:inset 0 0 15px #0f1413;color:#a5c2b8;display:flex;flex-direction:column;align-items:center}.about-box .picture{height:200px;width:150px;margin:0 50px;background-image:url(/wannes-portfolio/static/media/wannes.e0dced35.jpg);background-size:cover;box-shadow:0 0px 10px #0f1413;border-radius:5px}.about-box .wrapper-about{font-family:"Quicksand";letter-spacing:1px;margin:0 30px}.about-box .wrapper-about h3{text-align:center;color:#1a8a6c}.about-box .wrapper-about h4{color:#18584a;margin-bottom:0;font-style:italic}.about-box .wrapper-about ul li{font-family:"Nunito";color:#18584a}@media(min-width: 812px){.about-box{flex-direction:row;justify-content:center}.about-box .picture{margin-right:20px}.about-box .wrapper-about{width:auto}.about-box .wrapper-about h3{text-align:start}}
.content-box{margin:0 auto;padding:10px}.content-items{height:auto}.type-title{background:#111414;background:linear-gradient(to right, rgba(15, 24, 24, 0.1) 0%, rgba(9, 22, 20, 0.4) 20%, rgba(9, 22, 20, 0.4) 80%, rgba(15, 24, 24, 0.1) 100%);filter:progid:DXImageTransform.Microsoft.gradient( startColorstr="#111414", endColorstr="#1b2221",GradientType=1 );font-size:400%;text-align:center;margin:50px 0;color:#a5c2b8;text-transform:uppercase;height:auto}@media(min-width: 812px){.content-items{display:flex;flex-flow:row wrap;justify-content:space-evenly}.content-items .viewbox{width:48%}.type-title{width:100%}.content-box{width:80%;padding:0}}@media(min-width: 1661px){.content-items .viewbox{width:30%}}
.viewbox{height:auto;width:100%;padding:15px;display:flex;flex-direction:column;justify-content:space-between;box-shadow:0 2px 5px #0f1413;border:solid 2px #1b2e28;border-radius:10px;margin:20px auto;background:linear-gradient(to bottom, rgba(9, 22, 20, 0.4) 0%, rgba(0, 0, 0, 0.05) 100%)}.item-title h1{width:100%;margin-top:0;font-size:170%;color:#a5c2b8;font-family:"Rubik"}.item-info p{margin:2% 0;color:#17725a}.item-links p{display:flex;flex-direction:row;justify-content:space-evenly;align-items:center}.item-links p a{height:auto;border:solid #2e463e 2px;border-radius:5px;color:#a5c2b8;text-decoration:none;font-size:110%;padding:5px;box-shadow:0 1px 5px #0f1413}.item-links p a:hover{box-shadow:inset 0 1px 5px #0f1413;color:#99aca5}.item-img{width:100%;height:auto;min-height:260px;padding:10px;overflow:hidden}@media(max-height: 567px),(min-width: 812px){.viewbox{flex-wrap:wrap}}
.contact-box{width:100%;height:auto;display:flex;flex-wrap:wrap;justify-content:center;padding:50px;box-shadow:0 5px 5px #0f1413}.socialmedia-svg{width:calc(30px + 2vw);height:calc(30px + 2vw);border:solid 2px #2e463e;border-radius:15px;box-shadow:0 1px 5px #0f1413;background:linear-gradient(to bottom, rgba(9, 22, 20, 0.4) 0%, rgba(0, 0, 0, 0) 100%);fill:#a5c2b8;padding:10px;margin:10px}.socialmedia-svg:hover{fill:#99aca5;box-shadow:inset 0 0 5px #0f1413;cursor:pointer}
.footer-box{width:100%;height:auto;padding:20px;background-color:rgba(9,12,12,.35);display:flex;flex-direction:column;justify-content:center;align-items:center}.footer-box p{color:#99aca5;margin:10px;word-break:break-word}.footer-box p span,.footer-box p a{color:#17725a}.footer-box p span:hover,.footer-box p a:hover{color:#2e463e}.footer-section{border:solid 2px #1e2522;border-radius:15px;background:linear-gradient(to bottom, rgba(9, 22, 20, 0.4) 0%, rgba(0, 0, 0, 0) 100%);height:auto;margin:10px}@media(min-width: 812px){.footer-box{flex-direction:row}.footer-section{min-height:150px}}
